import Vue from 'vue';
import Router from 'vue-router';
import index from '@/pages/index';
import board from '@/pages/board';
import goods from '@/pages/goods';
import mine from '@/pages/mine';
import publish from '@/pages/publish';
import message from '@/pages/message';
import admin from '@/pages/admin';
import modifyinfo from '@/pages/modifyinfo';
import modifypassword from '@/pages/modifypassword';
import modifypublish from '@/pages/modifypublish';
import notice from '@/pages/notice';
import search from '@/pages/search';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index',
      meta: {
        title: ' ',
      },
    }, {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        title: '',
      },
    }, {
      path: '/board',
      name: 'board',
      component: board,
      meta: {
        title: '',
      },
    }, {
      path: '/goods',
      name: 'goods',
      component: goods,
      meta: {
        title: '',
      },
    }, {
      path: '/mine',
      name: 'mine',
      component: mine,
      meta: {
        title: '',
      },
    }, {
      path: '/publish',
      name: 'publish',
      component: publish,
      meta: {
        title: '',
      },
    }, {
      path: '/message',
      name: 'message',
      component: message,
      meta: {
        title: '',
      },
    }, {
      path: '/admin',
      name: 'admin',
      component: admin,
      meta: {
        title: '',
      },
    }, {
      path: '/modifyinfo',
      name: 'modifyinfo',
      component: modifyinfo,
      meta: {
        title: '',
      },
    }, {
      path: '/modifypassword',
      name: 'modifypassword',
      component: modifypassword,
      meta: {
        title: '',
      },
    }, {
      path: '/modifypublish',
      name: 'modifypublish',
      component: modifypublish,
      meta: {
        title: '',
      },
    }, {
      path: '/notice',
      name: 'notice',
      component: notice,
      meta: {
        title: '',
      },
    }, {
      path: '/search',
      name: 'search',
      component: search,
      meta: {
        title: '',
      },
    }],
});



